.list-item {
  background-color: white;
  display: flex;
  flex-direction: row;
  max-width: 50%;
  width: 50%;
  justify-content: space-between;
  padding: 0.8rem;
  margin: 1rem;
  align-items: center;
  border-radius: 1rem;
}


.list-item-completed {
  text-decoration: line-through;
}

